import React from 'react'
import { useEffect } from 'react';
import './SizeChart.css'
const SizeChart = () => {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, [])

  return (
    <div id='' className="container my-font-futura">
      <section style={{ marginTop: '50px' }} id="" class="">
        <div class="col-sm-12 col-md-6">
          <h2 className='my-font-futura'>Detailed Guide</h2>
          <p className='my-font-futura'>A detailed breakdown of our sizes</p>
          <table class="size-info-table my-font-futura">
            <thead>
              <tr>
                <td></td>
                <td colspan="2" className='my-font-futura'>P/XS</td>
                <td colspan="2" className='my-font-futura'>S</td>
                <td colspan="2" className='my-font-futura'>M</td>
                <td colspan="2" className='my-font-futura'>L</td>
                <td className='my-font-futura'>XL</td>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td className='my-font-futura'>BUST</td>
                <td className='my-font-futura'>32</td>
                <td className='my-font-futura'>33</td>
                <td className='my-font-futura'>34</td>
                <td className='my-font-futura'>35</td>
                <td className='my-font-futura'>36</td>
                <td className='my-font-futura'>37</td>
                <td>38</td>
                <td>40</td>
                <td>41</td>
              </tr>
              <tr>
                <td>RIB CAGE</td>
                <td>27</td>
                <td>28</td>
                <td>29</td>
                <td>30</td>
                <td>31</td>
                <td>32</td>
                <td>33</td>
                <td>35</td>
                <td>36</td>
              </tr>
              <tr>
                <td>WAIST</td>
                <td>25</td>
                <td>26</td>
                <td>27</td>
                <td>28</td>
                <td>29</td>
                <td>30</td>
                <td>31</td>
                <td>33</td>
                <td>34</td>
              </tr>
              <tr>
                <td>HIGH HIP</td>
                <td>31</td>
                <td>32</td>
                <td>33</td>
                <td>34</td>
                <td>35</td>
                <td>36</td>
                <td>38</td>
                <td>39</td>
                <td>40</td>
              </tr>
              <tr>
                <td>LOW HIP</td>
                <td>35</td>
                <td>36</td>
                <td>37</td>
                <td>38</td>
                <td>39</td>
                <td>40</td>
                <td>41</td>
                <td>43</td>
                <td>44</td>
              </tr>
            </tbody>
          </table>
          <h2>Size Information</h2>
          <p>How to choose your size</p>
          <ol>
            <li>NADAASI is available in size XS-L</li>
            <li>The size you order must accommodate all measurements. That is, if your bust
              is a size XS and hips are a size S, you should order a size S</li>
            <li>If measurements fall between sizes, choose the bigger size.</li>
            <li>Fit can vary by fabrication; however, we try our best to keep it close.</li>
          </ol>
        </div>
        <div class="col-sm-12 col-md-6 my-font-futura">
          <h2>Size Guide</h2>
          <p>Our sizes, compared to EU sizes.</p>
          <table class="size-info-table-eu my-font-futura">
            <thead>
              <tr>
                <td colspan="2">SIZE</td>
                <td colspan="2">EU</td>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td colspan="2">XS</td>
                <td colspan="2">32-34</td>
              </tr>
              <tr>
                <td colspan="2">S</td>
                <td colspan="2">34-36</td>
              </tr>
              <tr>
                <td colspan="2">M</td>
                <td colspan="2">38-40</td>
              </tr>
              <tr>
                <td colspan="2">L</td>
                <td colspan="2">42-44</td>
              </tr>
              <tr>
                <td colspan="2">XL</td>
                <td colspan="2">44-46</td>
              </tr>
            </tbody>
          </table>
        </div>

      </section>
    </div>
  )
}

export default SizeChart
